<template>
  <div class="register-page">
    <h1 class="register-title">欢迎注册地质信息数字化平台</h1>
    <el-form class="register-form" ref="registerForm" :model="registerForm" :rules="registerRules">
      <el-form-item label="设置账号" prop="username">
        <el-input v-model="registerForm.username" placeholder="请设置账号"></el-input>
      </el-form-item>
      <el-form-item label="设置密码" prop="password">
        <el-input type="password" v-model="registerForm.password" placeholder="请设置密码" show-password></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="confirmPassword">
        <el-input type="password" v-model="registerForm.confirmPassword" placeholder="再次输入密码" show-password></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="mobile">
        <el-input v-model="registerForm.mobile" placeholder="请输入手机号码"></el-input>
      </el-form-item>
      <el-form-item label="" prop="smsCode" class="sms-code">
        <div style="display:flex;width:100%;height:60px;padding-top: 20px;">
            <el-input v-model="registerForm.smsCode" placeholder="请输入手机验证码"></el-input>
            <el-button  type="primary" class="send-sms-btn">发送验证码</el-button>
        </div>
      </el-form-item>
      <el-form-item label="用户姓名" prop="name">
        <el-input v-model="registerForm.name" placeholder="请输入用户姓名"></el-input>
      </el-form-item>
      <el-form-item label="工作单位" prop="company">
        <el-input v-model="registerForm.company" placeholder="请输入工作单位"></el-input>
      </el-form-item>
      <el-form-item label="行业领域" prop="industry">
        <el-select v-model="registerForm.industry" placeholder="请选择行业领域" style="width:100%">
          <el-option label="IT/互联网" value="it"></el-option>
          <el-option label="金融" value="finance"></el-option>
          <el-option label="医疗" value="medical"></el-option>
          <!-- 其他行业选项 -->
        </el-select>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="registerForm.email" placeholder="请输入邮箱"></el-input>
      </el-form-item>
      <el-button style="margin-top:15px" type="primary" class="register-btn" @click="register">立即注册</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      registerForm: {
        username: '',
        password: '',
        confirmPassword: '',
        mobile: '',
        name: '',
        company: '',
        industry: '',
        email: '',
        smsCode: ''
      },
      registerRules: {
        // 表单验证规则
        // 可根据需要添加各字段的验证规则
        username: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '请再次输入密码', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入用户姓名', trigger: 'blur' }
        ],
        company: [
          { required: true, message: '请输入工作单位', trigger: 'blur' }
        ],
        industry: [
          { required: true, message: '请选择行业领域', trigger: 'change' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' }
        ],
        smsCode: [
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    register() {
      // 表单提交逻辑
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          // 表单验证通过，执行注册逻辑
          // 可在此处向后端发送注册请求
          // 注册成功后可以进行跳转或其他操作
          console.log('注册成功');
        } else {
          console.log('表单验证失败');
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.register-page {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px 200px;
  /* margin-top: 7.5rem; */
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}

.register-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: #409EFF;
}

.register-form {
  width: 100%;
}

.sms-code {
  display: flex;
  align-items: center;
}

.send-sms-btn {
  margin-left: 10px;
}

.register-btn {
  width: 100%;
}
:deep(.el-form-item) {
    margin-bottom: 10px;
}
:deep(.el-form-item__content) {
    width:100%
}
</style>
